/*
 * The syntax dark mode styles.
 */

@mixin dark-syntax {

    /* syntax highlight colors from https://raw.githubusercontent.com/jwarby/pygments-css/master/monokai.css */
    .highlight pre {
        background-color: var(--highlight-bg-color);
    }

    .highlight .hll {
        background-color: var(--highlight-bg-color);
    }

    .highlight .c {
        color: #75715e;
    }

    /* Comment */
    .highlight .err {
        color: #960050;
        background-color: #1e0010;
    }

    /* Error */
    .highlight .k {
        color: #66d9ef;
    }

    /* Keyword */
    .highlight .l {
        color: #ae81ff;
    }

    /* Literal */
    .highlight .n {
        color: #f8f8f2;
    }

    /* Name */
    .highlight .o {
        color: #f92672;
    }

    /* Operator */
    .highlight .p {
        color: #f8f8f2;
    }

    /* Punctuation */
    .highlight .cm {
        color: #75715e;
    }

    /* Comment.Multiline */
    .highlight .cp {
        color: #75715e;
    }

    /* Comment.Preproc */
    .highlight .c1 {
        color: #75715e;
    }

    /* Comment.Single */
    .highlight .cs {
        color: #75715e;
    }

    /* Comment.Special */
    .highlight .ge {
        color: inherit;
        font-style: italic;
    }

    /* Generic.Emph */
    .highlight .gs {
        font-weight: bold;
    }

    /* Generic.Strong */
    .highlight .kc {
        color: #66d9ef;
    }

    /* Keyword.Constant */
    .highlight .kd {
        color: #66d9ef;
    }

    /* Keyword.Declaration */
    .highlight .kn {
        color: #f92672;
    }

    /* Keyword.Namespace */
    .highlight .kp {
        color: #66d9ef;
    }

    /* Keyword.Pseudo */
    .highlight .kr {
        color: #66d9ef;
    }

    /* Keyword.Reserved */
    .highlight .kt {
        color: #66d9ef;
    }

    /* Keyword.Type */
    .highlight .ld {
        color: #e6db74;
    }

    /* Literal.Date */
    .highlight .m {
        color: #ae81ff;
    }

    /* Literal.Number */
    .highlight .s {
        color: #e6db74;
    }

    /* Literal.String */
    .highlight .na {
        color: #a6e22e;
    }

    /* Name.Attribute */
    .highlight .nb {
        color: #f8f8f2;
    }

    /* Name.Builtin */
    .highlight .nc {
        color: #a6e22e;
    }

    /* Name.Class */
    .highlight .no {
        color: #66d9ef;
    }

    /* Name.Constant */
    .highlight .nd {
        color: #a6e22e;
    }

    /* Name.Decorator */
    .highlight .ni {
        color: #f8f8f2;
    }

    /* Name.Entity */
    .highlight .ne {
        color: #a6e22e;
    }

    /* Name.Exception */
    .highlight .nf {
        color: #a6e22e;
    }

    /* Name.Function */
    .highlight .nl {
        color: #f8f8f2;
    }

    /* Name.Label */
    .highlight .nn {
        color: #f8f8f2;
    }

    /* Name.Namespace */
    .highlight .nx {
        color: #a6e22e;
    }

    /* Name.Other */
    .highlight .py {
        color: #f8f8f2;
    }

    /* Name.Property */
    .highlight .nt {
        color: #f92672;
    }

    /* Name.Tag */
    .highlight .nv {
        color: #f8f8f2;
    }

    /* Name.Variable */
    .highlight .ow {
        color: #f92672;
    }

    /* Operator.Word */
    .highlight .w {
        color: #f8f8f2;
    }

    /* Text.Whitespace */
    .highlight .mf {
        color: #ae81ff;
    }

    /* Literal.Number.Float */
    .highlight .mh {
        color: #ae81ff;
    }

    /* Literal.Number.Hex */
    .highlight .mi {
        color: #ae81ff;
    }

    /* Literal.Number.Integer */
    .highlight .mo {
        color: #ae81ff;
    }

    /* Literal.Number.Oct */
    .highlight .sb {
        color: #e6db74;
    }

    /* Literal.String.Backtick */
    .highlight .sc {
        color: #e6db74;
    }

    /* Literal.String.Char */
    .highlight .sd {
        color: #e6db74;
    }

    /* Literal.String.Doc */
    .highlight .s2 {
        color: #e6db74;
    }

    /* Literal.String.Double */
    .highlight .se {
        color: #ae81ff;
    }

    /* Literal.String.Escape */
    .highlight .sh {
        color: #e6db74;
    }

    /* Literal.String.Heredoc */
    .highlight .si {
        color: #e6db74;
    }

    /* Literal.String.Interpol */
    .highlight .sx {
        color: #e6db74;
    }

    /* Literal.String.Other */
    .highlight .sr {
        color: #e6db74;
    }

    /* Literal.String.Regex */
    .highlight .s1 {
        color: #e6db74;
    }

    /* Literal.String.Single */
    .highlight .ss {
        color: #e6db74;
    }

    /* Literal.String.Symbol */
    .highlight .bp {
        color: #f8f8f2;
    }

    /* Name.Builtin.Pseudo */
    .highlight .vc {
        color: #f8f8f2;
    }

    /* Name.Variable.Class */
    .highlight .vg {
        color: #f8f8f2;
    }

    /* Name.Variable.Global */
    .highlight .vi {
        color: #f8f8f2;
    }

    /* Name.Variable.Instance */
    .highlight .il {
        color: #ae81ff;
    }

    /* Literal.Number.Integer.Long */
    .highlight .gu {
        color: #75715e;
    }

    /* Generic.Subheading & Diff Unified/Comment? */
    .highlight .gd {
        color: #f92672;
        background-color: #561c08;
    }

    /* Generic.Deleted & Diff Deleted */
    .highlight .gi {
        color: #a6e22e;
        background-color: #0b5858;
    }

    /* Generic.Inserted & Diff Inserted */

    /* -----  custom styles ------ */

    --highlight-bg-color: #252525;
    --highlighter-rouge-color: #de6b18;
    --highlight-lineno-color: #6c6c6d;
    --inline-code-bg: #272822;
    --code-header-text-color: #6a6a6a;
    --code-header-muted-color: rgb(60 60 60);
    --code-header-icon-color: rgb(86 86 86);
    --clipboard-checked-color: #2bcc2b;
    --filepath-text-color: #bdbdbd;

    .highlight {
        .gp {
            color: #818c96;
        }
    }

    pre {
        color: #bfbfbf;
    }

    /* override Bootstrap */
}